.odc-sidebar {
  background: #fff;
  bottom: 0;
  box-shadow: var(--pf-global--BoxShadow--md);
  overflow-x: hidden;
  overflow-y: auto;
  position: absolute;
  right: 0;
  transition: opacity 175ms ease-out, transform 225ms ease-out;
  top: 0;
  width: calc(100% - 15px);
  visibility: hidden;
  z-index: 5;
}

@media (min-width: 762px) {
  .odc-sidebar {
    max-width: 550px;
  }
}

.odc-sidebar__content {
  padding: var(--pf-global--spacer--lg) 0;
}

.odc-sidebar-appear {
  opacity: 0;
  transform: translateX(10%);
  visibility: visible;
}

.odc-sidebar-appear-active {
  opacity: 1;
  transform: translateX(0);
  visibility: visible;
}

.odc-sidebar-enter-done {
  visibility: visible;
}

.odc-sidebar-exit {
  opacity: 1;
  transform: translateX(0);
  visibility: visible;
}

.odc-sidebar-exit-active {
  opacity: 0;
  transform: translateX(10%);
  visibility: visible;
}

